import clsx from "clsx";
import React, { useState } from "react";
import "../../styles/tailwind.css";

export interface ButtonProps {
  /** Is this the principal call to action on the page? */
  fullColor?: boolean;
  /** What background color to use */
  backgroundColor?: string;
  /** How large should the button be? */
  width?: number | string;
  height?: number | string;
  /** Button contents */
  label: string;
  /** Optional click handler */
  onClick?: () => void;
  rounded?: boolean;
  className?: string;
  color?: string;
}

/** Primary UI component for user interaction */
export const Button = ({
  fullColor,
  width,
  height,
  backgroundColor,
  label,
  rounded,
  className,
  color,
  ...props
}: ButtonProps) => {
  const [hover, setHover] = useState(false);

  return (
    <button
      onMouseOver={() => {
        setHover(true);
      }}
      onMouseLeave={() => {
        setHover(false);
      }}
      type="button"
      className={`${className} flex items-center justify-center border-solid rounded-lg border-2 px-3 py-1 border-main-color text-main-color ${clsx(
        {
          [`text-white bg-main-color`]: (!color && hover) || fullColor,
          [`bg-base-color border-base-color`]: !color && hover && fullColor,
          [`text-white bg-[${color}]`]: color && hover,
          [`text-[${color}]`]: color,
          ["!rounded-3xl"]: rounded,
          // [`text-white bg-main-color`] : fullColor,
        }
      )}`}
      style={{ backgroundColor, width: width, height: height }}
      {...props}
    >
      {label}
    </button>
  );
};
